.custom-button {
  display: flex;
  justify-content: center;
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px 0 35px;
  font-size: 0.75vw;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-family: "Open Sans Condensed";
  font-weight: bolder;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
    transition: 300ms ease all;
  }

  &.google {
    background-color: #4285f4;
    color: white;
    text-align: center;

    &:hover {
      color: #4285f4;
      background-color: white;
      border: 1px solid #4285f4;
    }
  }

  &.inverted {
    background-color: white;
    color: black;
    text-align: center;
    border: 1px solid black;

    &:hover {
      color: white;
      background-color: black;
      border: none;
    }
  }
}
